<template>
    <div class="edit-setting">
        <h2 class="title">
            <slot></slot>
        </h2>
        <el-form :model="settingData" status-icon label-width="200px" class="form-group">
            <el-form-item label="类型" prop="type">
                <el-radio-group v-model="settingData.type">
                    <el-radio :label="0">文本</el-radio>
                    <el-radio :label="1">点击</el-radio>
                    <el-radio :label="2">关注</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="用户回复文本" prop="text" v-if="settingData.type == 0">
                <el-input type="text" v-model="settingData.text"></el-input>
            </el-form-item>
            <el-form-item label="点击关键词" prop="key" v-else-if="settingData.type == 1">
                <el-input type="text" v-model="settingData.key"></el-input>
            </el-form-item>
            <el-form-item label="消息模板ID" prop="msgId">
                <el-input type="text" v-model="settingData.msgId"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm()">提交</el-button>
                <el-button type="warning" @click="resetForm()">重置</el-button>
                <el-button type="info" @click="cancel()">取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import store from "../../../store.js";
    import Axios from "axios";
    export default {
      props: {
        data_info: Object
      },
      data() {
        return {
          settingData: {
            type: 0
          }
        };
      },
      mounted() {
        if (store.state.settingUpdate) {
          this.settingData = this.data_info;
        }
      },
      methods: {
        cancel() {
          store.commit("changeSettingTable", true);
          store.commit("changeSettingUpdate", false);
          this.$emit("showAgain");
        },
        resetForm() {
          this.settingData = {};
        },
        submitForm() {
          if (!store.state.settingUpdate) {
            Axios({
              url: "/reply/create",
              method: "post",
              data: this.settingData
            }).then(res => {
              this.cancel();
            });
          } else {
            Axios({
              url: "/reply/update",
              method: "post",
              data: {
                id: this.settingData._id,
                type: this.settingData.type,
                text: this.settingData.text,
                key: this.settingData.key,
                msgId: this.settingData.msgId
              }
            }).then(res => {
              this.cancel();
            });
          }
        }
      }
    };
</script>

<style scoped>
    .edit-setting {
      float: left;
    }
    .form-group {
      width: 500px;
    }
    .title {
      margin-bottom: 50px;
      margin-left: 200px;
    }
</style>



